html {
	height: 100%;
}

body {
	margin: 0;
	padding: 0;
	height: 100%;
}

.image-box-background {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

.image-box-logo {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-top: 150px;
}

.burger-button {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 30px;
	width: 36px;
	background: transparent;
	border: none;
	cursor: pointer;
	box-sizing: border-box;
	position: absolute;
	z-index: 100;
	top: 40px;
	left: 50px;
}

.burger-button:focus {
	outline: none;
}

.burger-button-line {
	width: 36px;
	height: 3px;
	background: white;
}

.burger-button-line-hover {
	width: 36px;
	height: 3px;
	background: lightblue;
}

.side-drawer {
	height: 100%;
	width: 23%;
	background: rgb(34, 50, 80);
	opacity: 70%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 200;
}

.side-drawer ul {
	height: 100%;
	list-style: none;
	display: flex;
	flex-direction: column;
	padding-top: 20px;
	padding-left: 40px;
	
}

.side-drawer li {
	margin: 10px;
	padding: 10px;
	
}

.side-drawer a {
	color: white;
	text-decoration: none;
	font-size: 30px;
	font-family: 'Playfair Display SC', serif;
}

.side-drawer a:hover {
	color: #dac8b2;
}

.backdrop {
	top: 0;
	left: 0;
	position: fixed;
	width: 100%;
	height: 100%;
	background: #20201f;
	opacity: 50%;
	z-index: 150;
}